/* unplugin-vue-components disabled */import { computed, ref, reactive } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import validateSchema from '@/utils/validate-schema';
export default {
  name: 'Set',

  setup() {
    //控制页面是否加载
    let loading = ref(false); //控制切换账号面板

    let changeAccountShow = ref(false); //当前是否为管理状态

    let isManage = ref(false); //控制登录表单显示隐藏

    let loginShow = ref(false); //获取登录过的账号

    let accountArr = computed(() => {
      return store.state.user.accountArr;
    }); //存储表单信息

    const userForm = reactive({
      zhanghao: '',
      mima: ''
    }); //获取表单

    const userFormRef = ref(null); //校验规则

    const loginRules = {
      zhanghao: [{
        required: true,
        trigger: 'blur',
        message: '用户名不能为空'
      }, {
        trigger: 'blur',
        validator: validateSchema.validateAccount
      }],
      mima: [{
        required: true,
        trigger: 'blur',
        message: '密码不能为空'
      }, {
        trigger: 'blur',
        validator: validateSchema.validatePassword
      }]
    };
    const store = useStore();
    const router = useRouter(); //设置中按钮点击事件

    const setBtnClick = async () => {
      try {
        //开启加载
        loading.value = true; //发送更新请求

        await store.dispatch('user/userInfoUpdate'); //更新成功提示

        ElMessage.success('数据更新成功'); //关闭加载

        loading.value = false;
      } catch (error) {
        ElMessage.error(error.message);
      }
    }; //退出登录/切换账号按钮


    const logout = () => {
      //弹出切换账号页面
      changeAccountShow.value = true;
    }; //关闭切换账号面板


    const closeChangeAccountShow = () => {
      changeAccountShow.value = false;
    }; //登录其他账号


    const login = async account => {
      if (account.active === true) {
        //如果点击的是当前已经在的账号
        //关闭账号切换账号面板
        changeAccountShow.value = false;
      } else {
        try {
          //清空本地储存
          store.commit('user/deleteAllInfo'); //发送登录请求

          await store.dispatch('user/login', {
            zhanghao: account.zhanghao,
            mima: account.mima
          }); //发送请求 获取用户token

          await store.dispatch('user/getUserToken', {
            zhanghao: account.zhanghao,
            mima: account.mima
          }); //提示登录成功

          ElMessage.success('切换账号成功'); //将当前账号状态改为active

          store.commit('user/changeActive', account.zhanghao); //页面进行跳转

          router.push('/home');
        } catch (error) {
          //失败
          ElMessage.success('账号切换失败，请重试');
          console.log(`切换账号:${error.message}`);
        }
      }
    }; //清空聊天记录


    const deleteAllChat = async () => {
      try {
        await ElMessageBox.confirm('确定要删除聊天记录吗？');
        store.commit('user/deletaAllChat');
        ElMessage.success('删除成功');
      } catch (error) {
        console.log(error.message);
        ElMessage.info('取消删除');
      }
    }; //删除登录过的某个账号


    const deleteAccount = account => {
      store.commit('user/deleteAccountArr', account.zhanghao);
    }; //添加账号


    const addAccount = () => {
      //登录表单显示
      loginShow.value = true;
    }; //关闭登录页


    const closeLogin = () => {
      //登录表单隐藏
      loginShow.value = false; //重置表单

      userFormRem.value.resetFields();
    }; //添加多的账号


    const addLoginAcccount = () => {
      //表单校验
      userFormRef.value.validate(async valid => {
        //如果校验通过
        if (valid) {
          try {
            //发送登录请求
            await store.dispatch('user/login', userForm); //发送请求 获取用户token

            await store.dispatch('user/getUserToken', userForm); //添加进已登录列表

            store.dispatch('user/addAccountArrAction'); //提示登录成功

            ElMessage.success('登录成功'); //页面进行跳转

            router.push('/home');
          } catch (error) {
            //方便测试
            console.log(`设置页面${error}`);
            ElMessage.error(error.message);
          }
        }
      });
    };

    return {
      loading,
      setBtnClick,
      logout,
      deleteAllChat,
      changeAccountShow,
      closeChangeAccountShow,
      isManage,
      accountArr,
      login,
      deleteAccount,
      addAccount,
      loginShow,
      closeLogin,
      userForm,
      loginRules,
      userFormRef,
      addLoginAcccount
    };
  }

};